import React, { Component } from 'react'
import axios from 'axios'

class Contact extends Component {
    state = {
        name: "",
        email: "",
        message: ""
    }

    handleChange = (e) => {
        this.setState({
        [e.target.name]: e.target.value
        })
    }
    
    // handelSumbit = (e) => {
    //     e.preventDefault()
    //      axios('http://localhost:5000/sendemail', {
    //          method: "POST",
    //          data: {
    //             name: this.state.name,   
    //             email: this.state.email,  
    //             messsage: this.state.message
    //         }
    //      })
    //      .then(res => {
    //          console.log("message sent", res)
    //      })
    //      .catch(err => {
    //          console.log("error", err)
    //      })

    //     console.log(this.state.name, this.state.email, this.state.message) 
    // }

    handelSumbit = (e) => {
        e.preventDefault()
         axios('http://localhost:5000/sendemail', {
             method: "POST",
             data: {
                name: this.state.name,   
                email: this.state.email,  
                messsage: this.state.message
            }
         })
         .then(res => {
             console.log("message sent", res)
         })
         .catch(err => {
             console.log("error", err)
         })

        console.log(this.state.name, this.state.email, this.state.message) 
    }
    render() {
        return (
            <div>
                <form onSubmit={this.handelSumbit}>
                    <input type="text" value={this.state.name} placeholder="name" name="name" onChange={this.handleChange} />
                    <input type="email" value={this.state.email} placeholder="email" name="email" onChange={this.handleChange}/>
                    <input type="message" value={this.state.message} placeholder="message" name="message" onChange={this.handleChange}/>
                    <button>send</button>
                </form>
            </div>
        )
    }
}


export default Contact 
